<template>
  <section>
    <div id="container"></div>
    <el-form label-position="right" label-width="110px" class="mapForm">
      <el-form-item label="">
      </el-form-item>
      <el-form-item label="" v-for="(item,index) in formPush" :key="index">
        <el-input v-model="item.name"></el-input>
        <el-button size="small" @click="removeMarker(item.latitude,index)"></el-button>
      </el-form-item>
    </el-form>
    <div class="mapSave">
      <el-button size="small" type="primary" @click="savePoints"></el-button>
    </div>
  </section>
</template>

<script>
import TMap from '../../assets/js/initmap'
export default {
  props:['opintsArr'],
  data(){
    return{
      mapObj:'',//
      markerObj:'',//
      formPush:[

      ],
      geometries:[]
    }
  },
  watch:{
    opintsArr(val){
      console.log(val)
      if(val){
        this.formPush = val
        this.geometries = []
        val.forEach(item=>{
          let obj = {
            position:new TMap.LatLng(item.latitude, item.longitude)
          }
          this.geometries.push(obj)
        })
        this.initTmap()
      }
    }
  },
  methods:{
    initTmap(){
      let _this = this;
      TMap.init().then((TMap)=>{
        var center = new TMap.LatLng(22.831779, 114.980945);
        //
        var map = new TMap.Map("container", {
          rotation: 20, //
          pitch: 0, //0~45
          zoom: 17, //
          center: center //
        });
        TMap.ImageTileLayer.createCustomLayer({
          layerId: '5f6b13185922',
          // layerId: '5f5f2ad23757',
          map: map
        }).then(customLayer => {
          if (customLayer) {// 
  
          } else {
            // 
            console.log('fail');
          }
        });
        _this.mapObj = map
        var markerLayer = new TMap.MultiMarker({
            id: 'marker-layer',
            map: map,
            geometries:_this.geometries
        });
        _this.markerObj = markerLayer
        map.on('click',function(e){
          console.log(e)
          let obj = {
              name:'',
              latitude:"",
              longitude:"",
            }
          obj.latitude= e.latLng.getLat();
          obj.longitude = e.latLng.getLng();
          _this.formPush.push(obj)
          markerLayer.add({
            id:obj.latitude,
            position: e.latLng
          }); 
 console.log(e.latLng.getLng()+','+e.latLng.getLat())
});
      })
    },
    removeMarker(id,index){
      this.markerObj.remove(id)
      this.formPush.splice(index,1)
      console.log(this.formPush)
    },
    savePoints(){//
      console.log(this.formPush)
      this.$emit("mapOpintsArr",this.formPush)
    }
  },
  mounted(){
    if(!this.opintsArr){
        this.initTmap()
    }

  }
}
</script>

<style scoped>
  #container{
    width:100%;
    height:400px;
  }
  .mapSave{
    text-align: center;
  }
</style>


