<template>
  <section class="modlude">
    <!-- <el-form :inline="true" :model="formSearch" class="form_inline" label-width="80px">

      <el-form-item label="" size="small">
        <el-input v-model="formSearch.name" placeholder=""></el-input>
      </el-form-item>

      <el-form-item>
        <el-button size="small" @click="getInit"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="showAdd"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="danger" @click="deleInfor(false)"></el-button>
      </el-form-item>
    </el-form> -->
    <searchModule  @muchDeleteType="getMuchDeleteType" @searchInfor="getSearchInfor" @addTotrue="getAddTotrue" :formSearch="formSearch" :formItems="formItems" :showAddBtn="showAddBtn" :showDelBtn="showDelBtn"></searchModule>
    <el-table ref="multipleTable" :data="formData" style="width: 100%" stripe @select="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="name"label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="department" label=""></el-table-column>
      <el-table-column prop="idNumber" label=""></el-table-column>
      <el-table-column prop="phone" label=""></el-table-column>
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column prop="sex" label="">
        <template slot-scope="scope">
          {{scope.row.sex==1?"":""}}
        </template>
      </el-table-column>
      <el-table-column prop="gmtModified" label="">
        <template slot-scope="scope" v-if="scope.row.gmtModified">
          {{$root.getDateArray(Number(scope.row.gmtModified))[9]}}
        </template>
      </el-table-column>
      <el-table-column prop="gmtCreate" label="">
        <template slot-scope="scope" v-if="scope.row.gmtCreate">
          {{$root.getDateArray(scope.row.gmtCreate)[9]}}
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right" width=200 v-if="!showBtnFather">
				<template slot-scope="scope">
					<el-button type="warning" size="small" @click="updateShowBox(scope.row),updateDialog = true"> </el-button>
					<el-button type="danger" size="small" @click="deleInfor(scope.row.id)"> </el-button>
          <!-- <el-button type="primary" size="small" @click="updateShowBox(scope.row),detailsDialog=false"></el-button> -->
				</template>
			</el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>
      <div class="export_template" style="margin-bottom:20px;" v-if="showBtnFather">
        <el-button size="small" @click="upshRenyuan" type="primary"> </el-button>
      </div>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialog">
      <div class="cont_box_left">
        <el-form label-position="right" label-width="110px" :model="formPush" :rules="rules"  ref='addList'>
         <el-form-item label="" prop="name">
           <el-input v-model="formPush.name"></el-input>
         </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model="formPush.phone"></el-input>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="formPush.address"></el-input>
          </el-form-item>
          <el-form-item label="" prop="department">
            <el-input v-model="formPush.department	"></el-input>
          </el-form-item>
          <el-form-item label="" prop="idNumber">
            <el-input v-model="formPush.idNumber"></el-input>
          </el-form-item>
          <el-form-item label="" prop="title">
            <el-input v-model="formPush.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-radio-group v-model="formPush.sex">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" >
            <el-select v-model="formPush.type">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
              <el-option label="" :value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addList('addList')"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog">
      <div class="cont_box_left">
        <el-form label-position="right" :rules="rules" label-width="110px" :model="formUpdate" ref='formUpdate'>
         <el-form-item label="" prop="name">
           <el-input v-model="formUpdate.name"></el-input>
         </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model="formUpdate.phone"></el-input>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="formUpdate.address"></el-input>
          </el-form-item>
          <el-form-item label="" prop="department">
            <el-input v-model="formUpdate.department	"></el-input>
          </el-form-item>
          <el-form-item label="" prop="idNumber">
            <el-input v-model="formUpdate.idNumber"></el-input>
          </el-form-item>
          <el-form-item label="" prop="title">
            <el-input v-model="formUpdate.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-radio-group v-model="formUpdate.sex">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" >
            <el-select v-model="formUpdate.type">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
              <el-option label="" :value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium" @click="updateDialog = false"> </el-button>
        <el-button size="medium" @click="updateList('formUpdate')"></el-button>
      </div>
    </el-dialog>

  </section>
</template>

<script>
import paging from '../paging'
import searchModule from '../searchModule'
import { queryStaff,addStaff,editStaff,delStaff, } from '../../url/api.js';
export default {
  props:["showBtnFather"],
  data(){
    return{
      imageUrl:'',
      showAddBtn:this.showBtnFather?false:true,//
      showDelBtn:true,//
      formItems:{//label
        formItemsArr:[
          {
            name:'',
            filed:"name",
            type:'text'
          },
          {
            name:'',
            filed:"address",
            type:'text'
          },
          {
            name:'',
            filed:"phone",
            type:'text'
          },
          {
            name:'',
            filed:"department",
            type:'text'
          },
          {
            name:'',
            filed:"idNumber",
            type:'text'
          },
          {
            name:'',
            filed:"title",
            type:'text'
          },
          {
            name:'',
            filed:"sex",
            type:'radio',
            options:[
              {
                label:1,
                nanme:'',
              },
              {
                label:2,
                nanme:'',
              },
            ]
          },
          {
            name:'',
            filed:"type",
            type:'select',
            options:[
              {
                label:'',
                value:1,
              },
              {
                label:'',
                value:2,
              },
              {
                label:'',
                value:3,
              },
            ]
          }
        ],
        labelWidth:'80px'
      },
      deleBatch:[],
      formSearch:{//
        current:1,
        type:this.showBtnFather||'',
        size: 10
      },
      formPush:{//

      },//
      formData: [],//
      formUpdate:{},//
      total: 0,//
      addDialog:false,
      updateDialog:false,
      rules: {
        name:[{ required: true, message: '',trigger: 'blur'}],
        department:[{ required: true, message: '',trigger: 'blur'}],
        address:[{ required: true, message: '',trigger: 'blur'}],
        idNumber:[{ required: true, message: '',trigger: 'blur'}],
        phone:[{ required: true, message: '',trigger: 'blur'}],
        title:[{ required: true, message: '',trigger: 'blur'}],
        sex:[{ required: true, message: '',trigger: 'change'}],
      },
      mapObj:'',
      markerObj:''
    }
  },
  methods:{
    upshRenyuan(){//
      this.$emit("getRenyuanIds",this.deleBatch)
    },
    getlist(){
      queryStaff(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){//
      this.getlist()

    },
    addList(addList){//

      this.$refs[addList].validate((valid) => {
        if (valid) {
          addStaff(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.getlist()
              this.$message({
                message: '',
                type: 'success'
              })
            }else{
              this.$message('')
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },


    updateList(formUpdate){//
    console.log(this.formUpdate)
    this.$refs[formUpdate].validate((valid) => {
      if (valid) {
        editStaff(this.formUpdate).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message({
              message: '',
              type: 'warning'
            });
            this.getInit()
          }
        })
        this.updateDialog = false
      } else {
        console.log('error submit!!');
        return false;
      }
    });
    },
    deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }
      if(this.deleBatch.length!=0){
        this.deleBatch.filter((item)=>{
          arrId.push(item.id)
          return item
        })
      }
      this.$confirm('')
      .then(_ => {
        delStaff(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getInit()
          }
        })
      })
      .catch(_ => {});
    },

    handleSelectionChange(val,self) {//
      this.deleBatch = val
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getlist()
    },
    updateShowBox(item){//
      this.formUpdate = {
        address:item.address,
        name:item.name,
        id:item.id,
        department:item.department,
        idNumber:item.idNumber,
        coverImg:item.coverImg,
        phone:item.phone,
        sex:item.sex,
        title:item.title,
        type:item.type,
      }
        console.log(this.formUpdate,this.fileList)

      },

    getMuchDeleteType(val){//
      this.deleInfor(val)
    },
    getAddTotrue(val){//
      this.addDialog=val
      // if(this.markerObj){
      //   this.markerObj.setMap(null)
      // }
      
      if(!this.mapObj){
        this.initTmap()
      }
      
    },
    getSearchInfor(val){//
      this.formSearch=val
      this.getlist()
    },
  },
  mounted(){
    this.getInit()
    // this.initAmap()
  },
  components:{
    paging,
    searchModule
  }
}
</script>
<style scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .cont_box{
    width:100%;
    display: flex;
  }
  .cont_box_left,.cont_box_right{
    flex:1;
    background: transparent ;
    padding:10px;
  }
  .cont_box_left{
    margin-right:10px;
  }
  .margin10{
    margin-bottom:10px;
  }
  .marginRight{
    margin-bottom:10px;
    border-bottom: 1px solid #eee;
  }
  .cont_box_right .el-form-item{
    margin-bottom:20px;
  }
#container{
  width:100%;
  height:200px;
}
</style>



