<template>
  <section class="modlude">
    <!-- <el-form :inline="true" :model="formSearch" class="form_inline" label-width="80px">

      <el-form-item label="" size="small">
        <el-input v-model="formSearch.name" placeholder=""></el-input>
      </el-form-item>

      <el-form-item>
        <el-button size="small" @click="getInit"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="showAdd"> </el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="danger" @click="deleInfor(false)"></el-button>
      </el-form-item>
    </el-form> -->
    <searchModule  @muchDeleteType="getMuchDeleteType" @searchInfor="getSearchInfor" @addTotrue="getAddTotrue" :formSearch="formSearch" :formItems="formItems" :showAddBtn="showAddBtn" :showDelBtn="showDelBtn"></searchModule>
    <el-table ref="multipleTable" :data="formData" style="width: 100%" stripe @select="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="name"label=""></el-table-column>
      <el-table-column prop="num" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="runTime" label="">
        <template slot-scope="scope" v-if="scope.row.runTime">
          {{$root.getDateArray(Number(scope.row.runTime))[9]}}
        </template>
      </el-table-column>
      <el-table-column prop="gmtModified" label="">
        <template slot-scope="scope" v-if="scope.row.gmtModified">
          {{$root.getDateArray(Number(scope.row.gmtModified))[9]}}
        </template>
      </el-table-column>
      <el-table-column prop="gmtCreate" label="">
        <template slot-scope="scope" v-if="scope.row.gmtCreate">
          {{$root.getDateArray(scope.row.gmtCreate)[9]}}
        </template>
      </el-table-column>
      <el-table-column prop="isDel" label="">
        <template slot-scope="scope">
          {{scope.row.isDel==1?"":""}}
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right" width=200 v-if="!showBtnFather">
				<template slot-scope="scope">
					<el-button type="warning" size="small" @click="updateShowBox(scope.row),updateDialog = true"> </el-button>
					<el-button type="danger" size="small" @click="deleInfor(scope.row.id)"> </el-button>
          <!-- <el-button type="primary" size="small" @click="updateShowBox(scope.row),detailsDialog=false"></el-button> -->
				</template>
			</el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>
    <div class="export_template" style="margin-bottom:20px;" v-if="showBtnFather">
      <el-button size="small" @click="upshDevInfor" type="primary"> </el-button>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialog">
      <div class="cont_box_left">
        <el-form label-position="right" label-width="110px" :model="formPush" :rules="rules"  ref='addList'>
         <el-form-item label="" prop="name">
           <el-input v-model="formPush.name"></el-input>
         </el-form-item>
          <el-form-item label="">
            <el-input v-model="formPush.num"></el-input>
          </el-form-item>
          <el-form-item label="">
          <el-date-picker
    v-model="formPush.runTime"
    type="datetime"
    value-format="timestamp"
    placeholder="">
    </el-date-picker>
          </el-form-item>
          <el-form-item label="" >
            <el-select v-model="formPush.type">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
              <el-option label="" :value="3"></el-option>
              <el-option label="" :value="4"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="mapOpints"></el-input>
            <el-button size="mini" @click="mapDialog = true"> </el-button>
          </el-form-item>
        </el-form>
        <!-- <div id="container"></div> -->
        <el-form label-position="right" label-width="110px">
          
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addList('addList')"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog">
      <div class="cont_box_left">
        <el-form label-position="right" :rules="rules" label-width="110px" :model="formUpdate" ref='formUpdate'>
         <el-form-item label="" prop="name">
           <el-input v-model="formUpdate.name"></el-input>
         </el-form-item>
          <el-form-item label="">
            <el-input v-model="formUpdate.num"></el-input>
          </el-form-item>
          <el-form-item label="" >
          <el-date-picker
    v-model="formUpdate.runTime"
    type="datetime"
    value-format="timestamp"
    placeholder="">
    </el-date-picker>
          </el-form-item>
          <el-form-item label="" >
            <el-select v-model="formUpdate.type">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
              <el-option label="" :value="3"></el-option>
              <el-option label="" :value="4"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="mapOpints"></el-input>
            <el-button size="mini" @click="mapDialog = true,aa()"> </el-button>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium" @click="updateDialog = false"> </el-button>
        <el-button size="medium" @click="updateList('formUpdate')"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="mapDialog">
      <div class="cont_box_left">
        <mapPoints @mapOpintsArr="mapOpintsArr" :opintsArr="opintsArr"></mapPoints>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import paging from '../paging'
import mapPoints from './mapPoints'
import searchModule from '../searchModule'
import TMap from '../../assets/js/initmap'

import { queryDevice,addDevice,editDevice,delDevlist } from '../../url/api.js';
export default {
  props:['showBtnFather'],
  data(){
    return{
      mapOpints:'',
      mapDialog:false,
      showAddBtn:this.showBtnFather==1?false:true,//
      showDelBtn:true,//
      formItems:{//label
        formItemsArr:[
          {
            name:'',
            filed:"runTime",
            type:'datePicker'
          },
          {
            name:'',
            filed:"name",
            type:'text'
          },


        ],
        labelWidth:'100px'
      },
      deleBatch:[],
      formSearch:{//
        current:1,
        size: 10
      },
      formPush:{//
        items:[
        ]
      },//
      formData: [],//
      formUpdate:{},//
      total: 0,//
      addDialog:false,
      updateDialog:false,
      rules: {
        name:[{ required: true, message: '',trigger: 'change'}],
        coverImg:[{ required: true, message: '',trigger: 'change'}],
        coverVideo:[{ required: true, message: '',trigger: 'change'}],
      },
      mapObj:'',
      markerObj:'',
      opintsArr:[],
      opintsArr1:[]
    }
  },
  methods:{
    aa(){
      setTimeout(item=>{
        this.opintsArr = this.opintsArr1
        console.log(this.opintsArr)
      },5000)
      
    },
    mapOpintsArr(val){//
      this.mapOpints=''
      this.formPush.items = val
      if(val.length!=0){
        val.forEach(item=>{
          this.mapOpints = item.name+","+this.mapOpints
        })
      }
      this.mapDialog=false
    },
    upshDevInfor(){//
      this.$emit("getDevIds",this.deleBatch)
    },
    initTmap(){
      let _this = this;
      TMap.init().then((TMap)=>{
        var center = new TMap.LatLng(22.831779, 114.980945);
        //
        var map = new TMap.Map("container", {
          rotation: 20, //
          pitch: 0, //0~45
          zoom: 17, //
          center: center //
        });
        TMap.ImageTileLayer.createCustomLayer({
          layerId: '5f6b13185922',
          // layerId: '5f5f2ad23757',
          map: map
        }).then(customLayer => {
          if (customLayer) {// 
  
          } else {
            // 
            console.log('fail');
          }
        });
        _this.mapObj = map
        var markerLayer = new TMap.MultiMarker({
            id: 'marker-layer',
            map: map
        });
        _this.markerObj = markerLayer
        map.on('click',function(e){
          console.log(e)

          _this.formPush.latitude= e.latLng.getLat();
          _this.formPush.longitude = e.latLng.getLng();
          markerLayer.add({
            position: e.latLng
          }); 
console.log(e.latLng.getLng()+','+e.latLng.getLat())
}); 
      })
    },
    getlist(){
      queryDevice(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){//
      this.getlist()

    },
    addList(addList){//
      if(this.formPush.items.length!=this.formPush.num){
        this.$message("")
        return
      }
      this.$refs[addList].validate((valid) => {
        if (valid) {
          addDevice(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.getlist()
              this.$message({
                message: '',
                type: 'success'
              })
            }else{
              this.$message('')
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },


    updateList(formUpdate){//
    console.log(this.formUpdate)
      if(this.formUpdate.items.length!=this.formUpdate.num){
        this.$message("")
        return
      }
      this.$refs[formUpdate].validate((valid) => {
        if (valid) {
          editDevice(this.formUpdate).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.$message({
                message: '',
                type: 'warning'
              });
              this.getlist()
            }
          })
          this.updateDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }
      if(this.deleBatch.length!=0){
        this.deleBatch.filter((item)=>{
          arrId.push(item.id)
          return item
        })
      }
      this.$confirm('')
      .then(_ => {
        delDevlist(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getlist()
          }
        })
      })
      .catch(_ => {});
    },

    handleSelectionChange(val,self) {//
      this.deleBatch = val
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getlist()
    },
    updateShowBox(item){//
      this.mapOpints = ''
      this.formUpdate = {
        items:item.items,
        name:item.name,
        id:item.id,
        num:item.num,
        runTime:item.runTime,
        serial:item.serial,
        type:item.type,
        longitude:item.longitude,
        latitude:item.latitude
      }
      this.opintsArr1 = item.items.filter(item=>{
        this.mapOpints = item.name+","+this.mapOpints
        return item
      })
        console.log(this.formUpdate,this.opintsArr)

      },

    getMuchDeleteType(val){//
      this.deleInfor(val)
    },
    getAddTotrue(val){//
      this.addDialog=val
      // if(this.markerObj){
      //   this.markerObj.setMap(null)
      // }
      
      if(!this.mapObj){
        // this.initTmap()
      }
      
    },
    showMap(){

    },
    getSearchInfor(val){//
      this.formSearch=val
      this.getlist()
    },
  },
  mounted(){
    // console.log(this.showBtnFather)
    this.getInit()
    // this.initAmap()
  },
  components:{
    paging,
    mapPoints,
    searchModule
  }
}
</script>
<style scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .cont_box{
    width:100%;
    display: flex;
  }
  .cont_box_left,.cont_box_right{
    flex:1;
    background: transparent ;
    padding:10px;
  }
  .cont_box_left{
    margin-right:10px;
  }
  .margin10{
    margin-bottom:10px;
  }
  .marginRight{
    margin-bottom:10px;
    border-bottom: 1px solid #eee;
  }
  .cont_box_right .el-form-item{
    margin-bottom:20px;
  }
#container{
  width:100%;
  height:200px;
}
</style>



